/*************************/
/****** General CSS ******/
/*************************/
body{
    background: #121212 !important;
}

.btn-inactive{
    pointer-events: none;
}

/*************************/
/********* Home *********/
/*************************/
.background{
    background-image: url("./assets/background.jpg") !important;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    min-height: calc(100vh - 60px);
}

.boxes-container{
    height: calc(100vh - 60px);;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

@media screen and (max-width: 1340px) {
    .boxes-container{
        height: 100% !important;
    }
}

.box{
    backdrop-filter: blur(5px) saturate(150%);
    -webkit-backdrop-filter: blur(5px) saturate(150%);
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.125);


    box-sizing: content-box;
    height: 30em;
    width: 40em;
    border-radius: 1vw;
    margin: 1em;

    display: table;
    text-align: center;
}

.box-container{
    vertical-align: middle;
    display: table-cell;
}

.landing-description{
    margin: 0em 1em 1vh 1em;
    color: white;
    text-align: start;
}


.features-list{
    text-align: start;
    margin-left: 3em;
}

.login-register{
    display: flex;
    justify-content: space-around;
    text-align: center;
    text-decoration: none;
    padding: 0vw 3vw 2vh 6vw;
}
  
.login-register h1{
    display: inline-block;
    font-size: 2.5em;
    color: white !important;
}

.login-register h1:hover{
    opacity: 75%;
}

.login-register h1:active{
    opacity: 50%;
}

.login-register .selected{
    border-bottom: 2px solid white;
}

.login-register .not-selected{
    border-bottom: 0px solid white;
}

.form-group{
    display: inline-block;
    width: 80%;
}

.form-item{
    margin-top: 1em;
}

/*************************/
/***** PreflopCharts *****/
/*************************/
.preflop-charts-btns-container{
    text-align: center;
}

.btn-spacing{
    margin: 1em 1em 0.5em;
}

.btn-group-spacing{
    margin-bottom: 1vh;
}

/*************************/
/****** RangeChart ******/
/*************************/
.range-chart-container{
    margin-top: 1em;
}

.square{
    text-align: center;
    user-select: none;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    margin: 1px;
    padding: 0px !important;
    width: calc(1.6vh + 1.6vw) !important;
    height: calc(1.6vh + 1.6vw) !important;
    font-size: calc(0.625vh + 0.625vw) !important;
    color: black;
    text-shadow: #F8F9FA;
}

/****************************/
/***** EquityCalculator *****/
/****************************/
.eq-calc-container{
    text-align: center;
}

.player-bar{
    margin: 0.5em;
    width: 95%;
    justify-content: center;
}

.player-bar-btn{
    max-width: fit-content;
    font-size: 1em !important;
}

.player-bar-range{
    width: 50em;
    text-align: left !important;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 50%;
}

.player-bar-calc-vals{
    font-size: xx-small !important;
}

.player-range-modal-square:hover{
    background-color: yellow !important;
    filter: brightness(75%);
}

.player-range-modal-square:active{
    background-color: yellow !important;
    filter: brightness(50%);
}

/*************************/
/********* Board *********/
/*************************/
.board{
    width: 100%;
    justify-content: center;
    text-align: center;
    margin: 0px !important;
}

.board:hover{
    filter: brightness(85%);
}

.board:active{
    filter: brightness(50%);
}

.board-playing-card{
    width: calc(3vh + 3vw) !important;
    margin-inline: 1vw;
    padding: 0px !important;
}

.board-modal-card{
    text-align: center;
    font-size: 0.75em;
    padding-inline: 0.5vw !important;
}

.board-modal-card.black{
    display: inline-block;
    border: 1px solid black;
    border-radius: 3px;
    margin: 1px;
}

.board-modal-card.red{
    display: inline-block;
    border: 1px solid red;
    border-radius: 3px;
    color: red;
    margin: 1px;
}

.board-modal-card:hover{
    background-color: #C0C0C0;
    filter: brightness(75%);
}

.board-modal-card:active{
    filter: brightness(50%);
}